import React from "react";
import "./Styles/Promocion.scss";
import web01 from "../images/web01.png";
import Boton from "./Boton";
 
function Promocion() {
  return (
    <div className="container-fluid promocion">     
      <div className="  container promocion_container">
        <p className="container">Sí! leístes bien, desarrollaremos tu pagina web, con las funcionalidades y diseño adaptados para tu empresa, y si el resultado no cumple tus expectativas <span>No Pagas</span></p>

        <div className="promocion_listado row">

          <div className="col-12 col-md-6">
            <p> Tu pagina web tendra lo siguiente:</p>
            <ul>
              <li>Diseño Web adabtables a Pc, Tablets y Smartphones </li>
              <li>Base de Datos</li>
              <li>
                Hosting <span>Gratis</span>
              </li>
              <li>
                Dominio .com <span>Gratis</span>
              </li>
              <li>Textos profesionales </li>
              <li>Galeria</li>
              <li>Alta en todos los Buscadores</li>
              <li>Localizacion en Google Maps </li>
              <li>Presencia en Google * </li>
              <li>Integracion con WhatsApp </li>
              <li>sistema de administración </li>
              <li>
                Bonus: 1 Modificación mensual <span>Gratis</span> por 3 meses
              </li>
            </ul>
          </div>

          <div className="col-12 col-md-6 align-self-center">
            <img className="img-fluid" src={web01} alt="web" />
          </div>
        </div>
      </div>

      <p className="text-center solicitud">
        Solo aceptaremos <span>10 solicitudes </span> para esta oferta!
      </p>

      <div className="testimonios_famosos row">
        <article>
          <img
            src="https://diario16.com/wp-content/uploads/2020/04/bill-gates.jpeg"
            alt="Bill Gate"
          />
          <h4>Bill Gates</h4>
          <span> Fundador de Microsoft</span>
          <p>“Si tu negocio no está en internet, tu negocio no existe”</p>
        </article>
        <article>
          <img
            src="https://upload.wikimedia.org/wikipedia/commons/d/dc/Steve_Jobs_Headshot_2010-CROP_%28cropped_2%29.jpg"
            alt="steve jobs"
          />
          <h4>Steve Jobs</h4>
          <span> Fundador de Apple</span>
          <p>
            “La proxima generacion de millonarios provendra de los negocios por
            internet”
          </p>
        </article>
        <article>
          <i className="fas fa-users"></i>
          <h4>Nuevos clientes</h4>
          <span> Aumenta tus ventas</span>
          <p>
            {" "}
            Podrás acceder a nuevos clientes estén donde estén y fidelizar los
            que ya tienes.
          </p>
        </article>
        <article>
          <i className="far fa-calendar-alt"></i>
          <h4>Horarios</h4>
          <span> 24hr, 365 dias</span>
          <p>
            Podrás tener tu negocio abierto al público 24 horas al día, 365 días
            al año sin gastos de alquiler, personal, luz.
          </p>
        </article>
        <article>
          <i className="fas fa-globe-americas"></i>
          <h4>Presencia</h4>
          <span> Nacional e internacional</span>
          <p>
            {" "}
            Tus productos y servicios serán visibles para tus potenciales
            clientes desde cualquier parte del mundo
          </p>
        </article>
      </div>

      <div className="promocion_footer mb-4">
        <p>
          Hemos recibido 6 Solicitudes <br/> <span>Solo restan 4!</span>
        </p>
        <Boton />
      </div>
    </div>
  );
}

export default Promocion;
